

:root{
    --max-menu-width:80px
}

.Menu .menu_open_close,
.Menu .logo,
.Menu .menu_icon a,
.Menu .bottom_menu{
    display: flex;
    align-items: center;
    justify-content: center;
}

.Menu{
    width: var(--max-menu-width);
    min-height: 100vh;
    /* background-color: yellowgreen; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    transition: all 0.5s linear;
    background-color: #1f1f1f;
}

.Menu .close{
    animation: close 0.5s linear ease-in-out;
}

@keyframes close {
    0%{
        width: var(--max-menu-width);
    }
    100%{
        width: 0px;
    }
}

.Menu .open{
    animation: open 0.5s linear ease-in-out;
}

@keyframes open {
    0%{
        width: 0px;
    }
    100%{
        width: var(--max-menu-width);
    }
}


.Menu .logo{
  width: 100%;
  height: 100px;
  /* background-color: antiquewhite; */
}

.Menu .menu_icon{
    width: 100%;
    min-height: 350px;
    max-height: 500px;
    /* background-color: violet; */
}


.Menu .bottom_menu{
    width: 100%;
    height: 100px;
    /* background-color: turquoise; */
}

.Menu .menu_open_close{
    width: 20px;
    height: 40px;
    background-color: yellow;
    position: absolute;
    right: -10px;
    top: calc(50% - 20px);
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid orange;
}

.Menu .logo img{
    width: 50px;
}

.Menu .menu_icon{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.Menu .menu_icon a{
    width: 100%;
    height: 50px;
    /* background-color: yellow; */
    font-size: 24px;
    color: #fff;
    cursor: pointer;
}

.Menu .bottom_menu i{
    font-size: 28px;
    color: #fff;
    cursor: pointer;
}


@media (max-width: 600px) {
    .Menu {
        width: 0;
    }
    .Menu .menu_open_close{
        right: -20px;
    }
}