
.RightSection{
    width: 100%;
    min-height: 100vh;
    background-color: rgb(87, 85, 84);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.RightSection .upperRight{
    display: flex;
    width: 100%;
    color: aliceblue;
    /* background-color: aquamarine; */
    height: 60px;
    justify-content: center;
    align-items: center;
}
.RightSection .lowerRight{
    padding: 0;
    width: 85%;
    min-height: 85vh;
    /* background-color: chartreuse; */
    border: 1px solid grey;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.RightSection .lowerRight .attendenceMarker{
    /* width: 100%; */
    position: absolute ;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 56.5px;
    min-height: 20px;
    /* background-color: aqua; */
}
.confirmer {
    display: none; /* Initially hidden */
  }
.RightSection .lowerRight .confirmer.visible{
    position: absolute;
    width: 20vw;
    height: 50vh;
    background-color: blueviolet;
    display: block;
    border-radius: 10px;
    box-shadow: inset;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.RightSection .lowerRight .calender{
    width: 95%;
    min-height: 87%;
    background-color: bisque;
    border: 1px solid grey;
    border-radius: 10px;
}

.RightSection .lowerRight .calender.blurred {
    filter: blur(5px); /* Add blur effect */
  }
  